import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import style from './style';
import { route } from 'preact-router';


export default class Header extends Component {

    constructor(props) {
        super(props);
        this.handleSearchStats = this.handleSearchStats.bind(this);
    }

    handleSearchStats (event) {
        route('/miner-stats?address=' + this.input.value);
        event.preventDefault();
    }

    render() {
        return (
            <div>
                <header class={style.header}>
                    <Link href="/"><h1>BClashic.NotBcash.org</h1></Link>
                    <nav>
                        <Link activeClassName={style.active} href="/">Home</Link>
                        {/*<Link activeClassName={style.active} href="/miner-stats">Miner-stats</Link>*/}
                        {/*<Link activeClassName={style.active} href="/profile/john">John</Link>*/}
                    </nav>
                </header>
                <div class={style.minerform}>
                    <form onSubmit={this.handleSearchStats}>
                        <label>Miner stats: <input type="text" ref={(input) => this.input = input} placeholder="BCHC Address" /> <input type="submit" value="Go" /></label>
                    </form>
                </div>
            </div>
        );
    }
}



