import { h, Component } from 'preact';
import { Router } from 'preact-router';
import createHashHistory from 'history/createBrowserHistory';

import Header from './header';
import Home from '../routes/home';
import Miner from '../routes/miner';
// import Home from 'async!../routes/home';
// import Profile from 'async!../routes/profile';

export default class App extends Component {
  /** Gets fired when the route changes.
   *  @param {Object} event   "change" event from [preact-router](http://git.io/preact-router)
   *  @param {string} event.url The newly routed URL
   */
  handleRoute = e => {
    this.currentUrl = e.url;
  };

  render() {
    return (
      <div id="app" className="site-wrapper">
        <Header />
        <Router history={createHashHistory()} onChange={this.handleRoute}>
          <Home path="/" />
          {/*<Miner path="/miner-stats/" address="1LghW6gmfmuUvCti8o5MD1p5Jo69zrERBF" />*/}
          <Miner path="/miner-stats" />
        </Router>
      </div>
    );
  }
}



